import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomeComponent} from '../components/general/home/home.component';
import {RegisterComponent} from '../components/general/register/register.component';
import {LoginComponent} from '../components/general/login/login.component';
import {AccountComponent} from '../components/account/account/account.component';
import {ErrorComponent} from '../components/general/error/error.component';
import {AddComponent} from '../components/account/add/add.component';
import {AuthGuard} from './auth.guard';
import {ApplicationsComponent} from '../components/account/applications/applications.component';
import {PersonalComponent} from '../components/account/personal/personal.component';
import {PrivacyComponent} from '../components/account/privacy/privacy.component';
import {AboutComponent} from '../components/general/about/about.component';
import {ContactsComponent} from '../components/general/contacts/contacts.component';
import {ApplicationComponent} from '../components/account/application/application.component';
import {SearchComponent} from '../components/account/search/search.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'register', component: RegisterComponent},
  { path: 'login', component: LoginComponent},
  { path: 'account', component: AccountComponent, canActivate: [AuthGuard]},
  { path:  'error', component: ErrorComponent},
  { path:  'add', component: AddComponent, canActivate: [AuthGuard]},
  { path:  'applications', component: ApplicationsComponent, canActivate: [AuthGuard]},
  { path:  'applications/:id', component: ApplicationComponent, canActivate: [AuthGuard]},
  { path:  'personal', component: PersonalComponent, canActivate: [AuthGuard]},
  { path:  'privacy', component: PrivacyComponent, canActivate: [AuthGuard]},
  { path:  'about', component: AboutComponent},
  { path:  'contacts', component: ContactsComponent},
  { path:  'search', component: SearchComponent},
  // { path:  '**', redirectTo: '/error'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {

}
