import { Injectable } from '@angular/core';
import {MatSnackBar} from '@angular/material/snack-bar';

@Injectable({
  providedIn: 'root'
})
export class SnackbarsService {

  constructor(
    private snackBar: MatSnackBar
  ) { }

  show(message: string) {
    this.snackBar.open(message, 'OK', {
      duration: 2000,
    });
  }
}
