import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {AccountService} from '../../../services/account.service';
import {Subscription} from 'rxjs';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../tools/interfaces';

@Component({
  selector: 'app-applications',
  templateUrl: './applications.component.html',
  styleUrls: ['./applications.component.css']
})

export class ApplicationsComponent implements OnInit {

  @ViewChild('grid', {static: false}) gridRef: ElementRef;

  aSub: Subscription;
  userApps: AppData[];
  columns: number;

  constructor(
    private account: AccountService,
    private auth: AuthService
  ) { }

  loaded = false;

  ngOnInit(): void {
    this.adjustGrid();
    this.aSub = this.account.getAllApplications().subscribe(apps => {
      console.log('apps', apps);
      this.userApps = apps;
      this.loaded = true;
    }, error => {
      if (error.status === 401) {
        this.auth.logout();
      }
    });
  }

  adjustGrid() {
    this.columns = (window.innerWidth * 0.7) / 320 >> 0;
    console.log('here is width', this.columns, window.innerWidth); // = '100px';
  }
}
