import {Injectable} from '@angular/core';
import {AppData, Applicant, Application, ApplicationData, Class, MainData, NewUser, UserUpdate} from '../tools/interfaces';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {FormBuilder} from '@angular/forms';


@Injectable({
  providedIn: 'root'
})


export class AccountService {

  constructor(private http: HttpClient ) {
  }


  getMain(): Observable<MainData> {
    console.log('getting info...');
    return this.http.get<MainData>(`/api/account`);
  }

  // ===== Работа с заявками

  registerApplication(application: Application): Observable<Application> {
    console.log('posting app', application);
    return this.http.post<Application>('/api/account', application);
  }

  registerLogo(image: File): Observable<{ url: string }> {
    const fd = new FormData();
    if (image) {
      fd.append('image', image, image.name);
    }
    // fd.append('name', name);
    return this.http.post<{ url: string }>('/api/account/logo', fd);
  }

  getAllApplications(): Observable<AppData[]> {
    // console.log('getting info...');
    return this.http.get<AppData[]>(`/api/account/apps`);
  }

  getApplicationById(id: string): Observable<ApplicationData> {
    return this.http.get<ApplicationData>(`/api/account/apps/${id}`);
  }



  // ===== работа с заявителями

  saveNewApplicant(applicant: Applicant): Observable<Applicant> {
    return this.http.post<Applicant>(`/api/account/applicants`, applicant);
  }

  getAllApplicants(): Observable<Applicant[]> {
    return this.http.get<Applicant[]>(`/api/account/applicants`);
  }

  updateApplicant(applicant: Applicant): Observable<Applicant> {
    return this.http.patch<Applicant>(`/api/account/applicants/${applicant._id}`, applicant);
  }

  removeApplicant(applicant: Applicant): Observable<Applicant> {
    return this.http.delete<Applicant>(`/api/account/applicants/${applicant._id}`);
  }



}
