import {Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from 'rxjs';
import {ClassService} from '../../../services/class.service';
import {MatCheckboxChange} from '@angular/material/checkbox';
import {element} from 'protractor';
import {FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit, OnDestroy {

  public classes;
  public selectedClasses = [];
  public foundTrademarks = [];

  searchSub: Subscription;
  searchForm: FormGroup;

  onSearch = false;

  constructor(
    private classService: ClassService
  ) { }

  ngOnInit(): void {
    window.scrollTo(0, 0);

    this.classes = this.createClasses();

    this.searchForm = new FormGroup({
      query    : new FormControl(null, [Validators.required]),
      classes : new FormControl(null),
      strong : new FormControl(false)
    });
  }

  createClasses(): any[] {
    const res = [];

    for (let i = 0; i < 45; ++i) {
      res.push({
        classNumber: i + 1,
        selected: 'none'
      });
    }

    return res;
  }

  selectClass(cl: any) {

    const idx = this.classes.indexOf(cl);

    if (idx >= 0) {

      if (this.classes[idx].selected === 'none') {
        this.selectedClasses.push(this.classes[idx].classNumber);
        this.classes[idx].selected = 'selected';
      } else {
        const ix = this.selectedClasses.indexOf(cl);
        this.selectedClasses.splice(ix - 1, 1);
        this.classes[idx].selected = 'none';
      }
      // console.log(this.selectedClasses);
    }

  }

  search(): void {
    this.foundTrademarks = [];
    this.onSearch = true;
    this.searchForm.controls.classes.setValue(this.selectedClasses);
    console.log(this.selectedClasses.toString().replace(/,/g, '+'));
    this.searchSub = this.classService.search(this.searchForm.value).subscribe(response => {
      // console.log(response);
      this.onSearch = false;
      this.foundTrademarks = response;

    }, error => {
      if (error.status === 401) {
        console.log(error);
      }
    })
  }

  setAll($event: MatCheckboxChange) {
    const state = $event.checked ? 'selected' : 'none';
    if (!$event.checked) {
      this.selectedClasses = [];
    }
    this.classes.forEach(el => {
      el.selected = state;
      if ($event.checked) {
        this.selectedClasses.push(el.classNumber);
      }
    });
  }

  ngOnDestroy(): void {

    if (this.searchSub) {
      this.searchSub.unsubscribe();
    }
  }
}
