import {Injectable} from '@angular/core';
import {NewUser, UpdatePassword, User, UserUpdate} from '../tools/interfaces';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';
import {Router} from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private token = null;
  private userName = null;
  private email = null;

  constructor(
    private http: HttpClient,
    private router: Router
              ) {
  }
  register(user: NewUser): Observable<NewUser> {
    return this.http.post<NewUser>('/api/auth/register', user);
  }

  login(user: User): Observable<{token: string, name: string, email: string}> {
    return this.http.post<{token: string, name: string, email: string}>('/api/auth/login', user)
      .pipe(
        tap(({token, name, email}) => {
          localStorage.setItem('auth-token', token);
          localStorage.setItem('username', name);
          localStorage.setItem('email', email);
          console.log('here are you', name, email);
          this.setToken(token);
          this.setName(name);
          this.setEmail(email);
        })
      );
  }

  checkPassword(password: string): Observable<boolean> {
    console.log('i have a pass', password)
    return this.http.post<boolean>('/api/auth/check', password)
      .pipe(
        tap(val => {
          console.log('value is', val)
        })
      );
  }

  updatePassword(update: UpdatePassword): Observable<{ action: string }> {
    return this.http.patch<{action: string}>('/api/auth/update', update);
  }

  updateUserData(user: UserUpdate): Observable<UserUpdate> {
    return this.http.patch<UserUpdate>('/api/auth/edit', user)
      .pipe(
        tap((result) => {
          localStorage.setItem('username', result.username);
          localStorage.setItem('email', result.email);
          console.log('here are you', result.username, result.email)
          // this.setToken(token);
          this.setName(result.username);
          this.setEmail(result.email);
        })
      );

  }
  setToken(token: string) {
    this.token = token;
  }
  getToken(): string {
    return this.token;
  }
  setName(name: string) {
    this.userName = name;
  }
  getName(): string {
    return this.userName;
  }
  setEmail(email: string) {
    this.email = email;
  }
  getEmail(): string {
    return this.email;
  }
  isAuthenticated(): boolean {
    return !!this.token;
  }
  logout() {
    this.setToken(null);
    this.setName(null);
    localStorage.clear();
    this.router.navigate(['/login']);
  }

}
