import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'applicant'
})
export class ApplicantPipe implements PipeTransform {

  transform(value: string): string {
    return value === 'UL' ? 'Юридическое лицо' : 'Индивидуальный предприниматель';
  }

}

@Pipe({
  name: 'mode'
})
export class ModePipe implements PipeTransform {

  transform(value: string): string {
    return value === 'electric' ? 'Электронная форма' : 'Самостоятельно';
  }

}

@Pipe({
  name: 'trademark'
})
export class TrademarkPipe implements PipeTransform {

  transform(value: string): string {
    if (value === 'word') {
      return 'Словесный';
    } else if (value === 'image') {
      return 'Изобразительный';
    } else {
      return 'Комбинированный';
    }
  }

}

@Pipe({
  name: 'status'
})
export class StatusPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'created':
        return 'Создан';
      case 'waiting':
        return 'Ожидает оплаты услуг';
      case 'processing':
        return 'Подан в обработку';
      case 'checking':
        return 'Проверяется';
      case 'done':
        return 'Готов';
      case 'tax':
        return 'Ожидание оплаты госпошлины';
      default:
        return value;
    }
  }

}

@Pipe({
  name: 'options'
})
export class OptionPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'fullCheck':
        return 'Полная проверка товарного знака';
      case '':
        return 'Нет';
      default:
        return value;
    }
  }

}

@Pipe({
  name: 'doctypes'
})
export class DocTypesPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'tax':
        return 'Квитанция на госпошлину';
      case 'proxy':
        return 'Доверенность';
      case 'permission':
        return 'Согласие на обработку';
      default:
        return value;
    }
  }

}
