import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {AccountService} from '../../../services/account.service';
import {ActivatedRoute, Params} from '@angular/router';
import {Subscription} from 'rxjs';
import {Application, ApplicationData, Document} from '../../../tools/interfaces';
import {AuthService} from '../../../services/auth.service';

@Component({
  selector: 'app-application',
  templateUrl: './application.component.html',
  styleUrls: ['./application.component.css']
})
export class ApplicationComponent implements OnInit, OnDestroy {

  aSub: Subscription;
  paramSub: Subscription;

  id = '';
  application: ApplicationData;

  @ViewChild('href', {static: false}) hRef: ElementRef;

  constructor(
    private account: AccountService,
    private route: ActivatedRoute,
    private auth: AuthService
  ) { }

  ngOnInit(): void {
    this.fetch();
  }

  fetch() {
    this.paramSub = this.route.params.subscribe((params: Params) => {
      this.id = params.id;
    }, error => {
      if (error.status === 401) {
        this.auth.logout();
      }
    });

    this.aSub = this.account.getApplicationById(this.id).subscribe(app => {
      this.application = app;
      console.log('classes', this.application.classes);
    }, error => {
      if (error.status === 401) {
        this.auth.logout();
      }
    });

}

  ngOnDestroy(): void {
    if (this.aSub) {this.aSub.unsubscribe(); }
    if (this.paramSub) {this.paramSub.unsubscribe(); }
  }

  download(doc: Document) {
    const link = document.createElement('a');
    link.setAttribute('type', 'hidden');
    link.href = doc.url;
    link.download = doc.url.split('\\')[2];
    document.body.appendChild(link);
    link.click();
    link.remove();
    // this.hRef.nativeElement.href = doc.url;
    // this.hRef.nativeElement.click();
  }
}
